import React from 'react';
import { GenericPage } from '../components/shared/generic-page';
import { graphql } from 'gatsby';

const breadcrumbs = [
  {
    text: 'Начало',
    link: '/',
  },
  {
    text: 'Фондация',
  },
]

const FondationPage = ({ data }) => {
  return (
    <>
      <GenericPage
        title={"Фондация NetSurf"}
        description={'Фондация “NetSurf Монтана” е основана през 2006 година. Основната ѝ цел е да допринася за развитието на гражданското общество в страната'}
        breadcrumbs={breadcrumbs}
        showShareTab={'fondaciq-netsurf'}
      >
        <img src={data.fondationImage.childImageSharp.fluid.src} className='img-fluid' alt="Фондация NetSurf" />

        <p className='mt-4'>Фондация “NetSurf Монтана” е основана през 2006 година в гр. Монтана.
            Основната ѝ цел е да допринася за развитието на гражданското общество в страната, като подпомага и обслужва гражданските организации и осигурява ресурси за развитието на техния капацитет; да насърчава разбирателството и интегрирането в обществото; да поддържа и популяризира традиционните изкуства и занаяти; да организира, финансира и осъществява други различни дейности.</p>
        <p>Предмет на дейността на фондацията е образованието, изкуството, културата, здравеопазването, спорта, социалното подпомагане, екологията, човешките права и други.</p>
        <p>Финансирането на проектите ѝ се осъществява чрез дарителство от частни лица и от бизнеса.</p>
        <p> В резултат на своята активна дейност, фондацията изгради широка мрежа от съмишленици, доброволци и експерти, които се включват в събитията ѝ с желание, интерес и професионализъм.</p>
        <p>Фондацията се ръководи от Управителен съвет в състав:</p>
        <ul className='blue-ticks'>
          <li>Стефан Георгиев Цветков – Председател на УС</li>
          <li>Мая Силвиева Маркова – член на УС</li>
          <li>Мартин Любомиров Василев – член на УС</li>
        </ul>
        <p>За връзка с Фондация “NetSurf Монтана”: <a href='mailto:foundation@netsurf.bg'>foundation@netsurf.bg</a></p>
      </GenericPage>
    </>
  )
}

export const query = graphql`
  query Fondation {
    fondationImage: file(relativePath: {eq: "common/fondaciq-netsurf.png"}) {
      childImageSharp {
        fluid(maxHeight: 800) {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`;

export default FondationPage;